export const initialState = {
	user: null,
	prof_info: [],
}

const reducer = (state,action) => {
	switch(action.type){
		case 'SET_USER':
			return{
				...state,
				user: action.user
			}
		case 'SET_INFO':
			return{
				...state,
				prof_info: action.prof_info
			}
		default:
			return state;
	}
}

export default reducer;